    background-color: transparent;
    position: relative;
    z-index: 1;
    visibility: visible;
    opacity: 1;
    pointer-events: unset;
    box-shadow: none;
  }
}
.menu-nav .has-sub {
  justify-content: space-between;
  gap: 0.25rem;
}
.menu-nav .has-sub::after {
  content: "\e5cf";
  font-family: "Material Symbols Outlined";
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1;
}
.menu-nav .has-sub.clear-content::after {
  display: none;
}
.menu-open .menu-nav {
